body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background: #000000;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, rgb(255, 151, 4), #000000);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, rgb(255, 151, 4), #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  text-align: center;
}

.app-title {
  margin-top: 75px;
  margin-bottom: 50px;
  font-size: 76px;
  color: #000000;
  font-family: 'Bigelow Rules';
}